    display: flex;
    justify-content: center;
    height: 100vh;
    align-items: center;
}
.content {
    border: 3px solid gray;
    padding: 30px;
    display: flex;
    border-radius: 30px;
    justify-content: center;
    flex-direction: column;
    font-size: 35px;
    font-family: sans-serif;
    max-width: 600px;
}
.calendar {
    font-size: 25px;
    border: 3px solid gray;
    border-radius: 30px;
    padding-left: 5px;
    padding-right: 5px;
}
.current-day-info {
    border: 3px solid gray;
    padding: 10px 15px;
}
.fieldset {
    border: 3px solid gray;
    margin-bottom: 20px;
}
